<template lang="pug">
  .brew-ring(fluid)
    b-row
      b-col(cols="12" md="5" offset-md="1")
        .brew-ring__display
          .brew-ring__selection
            img(
              :src="selectedImageSrc"
            )
          b-row.brew-ring__rings
            b-col.brew-ring__ring(
              v-for="(image, index) in images" cols="2" :key="index"
              @click="clickImageHandler(index)"
              :class="{ 'brew-ring__ring--selected': index === selectedIndex }"
              )
              img(:src="image.path")
      b-col(cols="12" md="4" offset="1")
        .brew-ring__introduction
          .brew-ring__type
            p {{ type }} 系列
            h1.brew-ring__name {{ name }}
          .brew-ring__ring-info(v-for="ring in ringsInfo")
            .brew-ring__labels
              p.brew-ring__label 型號
              p.brew-ring__label 材質
              p.brew-ring__label 戒寬選擇
              p.brew-ring__label(v-if="ring.weight") 鑲鑽選擇
            .brew-ring__descriptions
              p.brew-ring__description {{ ring.model }}
              p.brew-ring__description {{ ring.material }}
              p.brew-ring__description {{ ring.width }}
              p.brew-ring__description(v-if="ring.weight") {{ ring.weight }}
          .brew-ring__weight(v-if="weight")
              p.brew-ring__label 鑲鑽選擇
              p.brew-ring__description {{ weight }}
          .brew-ring__weight(v-if="options")
              p.brew-ring__label 鑲鑽選擇
              p.brew-ring__description {{ options }}
          .brew-ring__note(v-if="note")
              p 圖為戒寬{{ note }}
        .brew-ring__back-button
          decorated-button(
            @click="clickHandler"
            :color="'golden'"
            :buttonDescription="'BACK'")
    Appointment
</template>
<script>
import brewRingInfo from '@/views/BrewRing/brewRingInfo';
import Appointment from '@/components/Appointment.vue';
import DecoratedButton from '@/components/DecoratedButton.vue';

export default {
  name: 'brewRing',
  data() {
    return {
      selectedIndex: 0,
    };
  },
  components: {
    Appointment,
    DecoratedButton,
  },
  computed: {
    selectedImageSrc() {
      return this.images[this.selectedIndex].path;
    },
    brewRingId() {
      return this.$route.params.id;
    },
    ringInfo() {
      return brewRingInfo[this.brewRingId];
    },
    images() {
      return this.ringInfo.images;
    },
    ringsInfo() {
      return this.ringInfo.rings;
    },
    weight() {
      return this.ringInfo.weight
        ? this.ringInfo.weight
        : '';
    },
    options() {
      return this.ringInfo.options;
    },
    note() {
      return this.ringInfo.note;
    },
    type() {
      return this.ringInfo.type;
    },
    name() {
      return this.ringInfo.name;
    },
  },
  methods: {
    clickHandler() {
      this.$router.push(`/brew-ring-series/${this.type.toLowerCase()}`);
    },
    clickImageHandler(index) {
      this.selectedIndex = index;
    },
  },
};

</script>

<style lang="scss" scoped>
@import '../../scss/_mediaQuery.scss';
@import '../../scss/_variable.scss';
.brew-ring {
  p {
    margin-bottom: 5px;
  }
  &__display {
    margin-top: 70px;
    width: 100%;
  }
  &__selection {
    img {
      width: 100%;
    }
  }
  &__rings {
    margin-top: 40px;
    display: flex;
  }
  &__ring {
    &:nth-child(1) {
      @include tablet-and-mobile-only {
        margin-left: 20px;
      }
    }
    padding-right: 0;
    &--selected {
      img {
        border: 1px solid #debe98;
        padding: 5px;
      }
    }
    img {
      width: 100%;
    }
  }
  &__introduction {
    margin-top: 90px;
    text-align: left;
    @include tablet-and-mobile-only {
      margin-top: 30px;
    }
  }
  h1, p {
    font-weight: 400;
  }
  h1 {
    color: #debe98;
  }
  &__type {
    color: #debe98;
    font-size: 17px;
  }
  &__name {
    font-size: 40px;
  }
  &__ring-info {
    display: flex;
    margin-top: 20px;
    p {
      font-size: 15px !important;
      line-height: 1.73 !important;
    }
    &:nth-child(3) {
      margin-top: 20px;
    }
  }
  &__descriptions {
    margin-left: 40px;
    p {
        letter-spacing: 0.5px;
        text-align: justify;
        color: #3e3a39;
    }
    @include tablet-and-mobile-only {
      margin-left: $brew-ring-desc-m-margin-left;
      p {
        font-size: $brew-ring-desc-m-font-size !important;
        line-height: $brew-ring-desc-m-line-height !important;
      }
    }
  }
  &__label {
    color: #debe98;
  }
  &__labels, &__weight {
    font-size: 14px;
    @include tablet-and-mobile-only {
      font-size: $brew-ring-desc-m-font-size !important;
      line-height: $brew-ring-desc-m-line-height !important;
      p {
        font-size: $brew-ring-desc-m-font-size !important;
        line-height: $brew-ring-desc-m-line-height !important;
      }
    }
  }
  &__weight {
    display: flex;
    .brew-ring__description {
      margin-left: 40px;
      @include tablet-and-mobile-only {
        margin-left: $brew-ring-desc-m-margin-left;
      }
    }
  }
  &__back-button {
    margin-top: 40px;;
  }
  &__note {
    margin-top: 20px;
    @include tablet-and-mobile-only {
      p {
        font-size: $brew-ring-desc-m-font-size !important;
        line-height: $brew-ring-desc-m-line-height !important;
      }
    }
  }
}
</style>
